import React  from 'react';
import Container from 'react-bootstrap/Container';
import background from "./assets/fondo.png";
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import noticias from "./assets/Noticias.png";
import { useEffect } from 'react';
import {NoticiasContext} from './index';

function Noticias() {

    useEffect(() => {
        console.log(NoticiasContext.value);
        //window.location.reload(false);
      },[]);


    return (
        <Container fluid style={{ backgroundImage: `url(${background})`, backgroundPosition:'center top', backgroundSize: '100% 100%' , height: '100vh'}} >
            <Row style={{ textAlign:'center', paddingTop: '2rem'}}>
                <Col>
                <img src={noticias} style={{width: '10vw', marginTop:'1rem'}} alt="Logo" />
                     <h1 style={{ color:'white'}}>Noticias</h1>
                </Col>
            </Row>
            <NoticiasContext.Consumer>
                {Facebook}
            </NoticiasContext.Consumer>
        </Container>
    );
}

function Facebook(){
    return(
        <Row className='justify-content-center' style={{paddingTop: '3rem'}}>
        <Col></Col>
        <Col>
            <div id="fb-root">
                <div className="fb-page justify-content-center" data-lazy='true' data-href="https://web.facebook.com/redpopulartrans/?_rdc=1&amp;_rdr" data-tabs="timeline" data-width="500" data-height="" data-small-header="false" data-adapt-container-width="true" data-hide-cover="false" data-show-facepile="true"><blockquote cite="https://web.facebook.com/redpopulartrans/?_rdc=1&amp;_rdr" className="fb-xfbml-parse-ignore"><a href="https://web.facebook.com/redpopulartrans/?_rdc=1&amp;_rdr">Red Popular Trans</a></blockquote></div>
            </div>
        </Col>
        <Col>
            <div id="fb-root">
            <div className="fb-page" data-href="https://web.facebook.com/Red-Comunitaria-Trans-255992844505189/?_rdc=1&amp;_rdr" data-tabs="timeline" data-width="500" data-height="" data-small-header="false" data-adapt-container-width="true" data-hide-cover="false" data-show-facepile="true"><blockquote cite="https://web.facebook.com/Red-Comunitaria-Trans-255992844505189/?_rdc=1&amp;_rdr" className="fb-xfbml-parse-ignore"><a href="https://web.facebook.com/Red-Comunitaria-Trans-255992844505189/?_rdc=1&amp;_rdr">Red Comunitaria Trans</a></blockquote></div>
            </div>
        </Col>
        <Col></Col>
        </Row>
    );
}

export default Noticias;
